import React from 'react';
import axios from 'axios';
import UploadFile from './Upload.js';
import { Button, Card, Container, Row, Col, Form, InputGroup } from 'react-bootstrap';
const geometric = require("geometric");

function line(props) {
    const { ctx, x, y, x1, y1 } = props;
    ctx.strokeStyle = 'red'
    ctx.beginPath();
    ctx.moveTo(x, y);
    ctx.lineTo(x1, y1);
    ctx.lineWidth = 2;
    ctx.stroke();
}

class MapLocation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            canvasWidth: 1000,
            canvasHeight: 800,
            background_src: `${axios.defaults.baseURL}/RFID/floor_image/38`,
            mouseStatus: false,
            drawStatus: false,
            count: 0,
            sx: 0,
            sy: 0,
            prex: 0,
            prey: 0,
            pList: [],
            preList: [],
            machine_data: [],
            canvas_data: [],
            nowx: [],
            nowy: [],
            floor_data: [],
            selected_floor_id: -1,
            new_floor_id: 0,
            floorName: '',
            displayBtnName: '',
            displayCheck: false,
            inputHidden: true,
            uploadHidden: true,
            addBtnHidden: false,
            canvasHidden: true,
            editMode: this.props.editMode,
            selectedFloor: this.props.selectedFloor
        };
        this.selectRef = React.createRef()
        this.downRef = React.createRef()
        this.midRef = React.createRef()
        this.upRef = React.createRef()
        this.updateCanvas = this.updateCanvas.bind(this);
    }

    componentDidMount() {
        this.selectRef.current.value = 38
        var ev2 = new Event('change', { bubbles: true });
        this.selectRef.current.dispatchEvent(ev2);
        let cur_floor_data = this.state.floor_data
        axios.get('/RFID/floor')
            .then(response => {
                response.data.map((value, index) => {
                    cur_floor_data.push({
                        floor_id: value.floor_id,
                        floor_name: value.floor_name
                    })
                    this.setState({
                        floor_data: cur_floor_data
                    })
                })
            })
            .catch(function (error) {
                console.log(error);
            })
        this.updateCanvas()
        window.addEventListener("resize", this.updateCanvas);
    }

    // componentDidUpdate(prevProps, prevState) {
    //     if (prevState.canvasWidth !== this.state.canvasWidth || prevState.canvasHeight !== this.state.canvasHeight) {
    //         this.selectRef.current.value = 1
    //         var ev2 = new Event('change', { bubbles: true });
    //         this.selectRef.current.dispatchEvent(ev2);
    //         this.updateCanvas()
    //         window.addEventListener("resize", this.updateCanvas);
    //     }
    // }

    componentWillUnmount() {
        window.removeEventListener("resize", this.updateCanvas);
    }

    updateCanvas() {
        let ratio;
        const ctx_down = this.downRef.current.getContext('2d');
        var background = new Image();
        background.src = this.state.background_src;
        background.onload = () => {
            ratio = this.upRef.current.offsetWidth / background.width;
            this.setState({
                canvasWidth: this.upRef.current.offsetWidth,
                canvasHeight: background.height * ratio
            })
            ctx_down.drawImage(background, 0, 0, this.state.canvasWidth, this.state.canvasHeight);
        }
    }

    handleMouseDown(e) {
        if (this.state.drawStatus) {
            let cur_pList = this.state.pList;
            let cur_preList = this.state.preList;
            cur_pList.push({
                px: e.nativeEvent.layerX,
                py: e.nativeEvent.layerY
            })
            const ctx = this.upRef.current.getContext('2d');
            if (this.state.count > 3) {
                this.setState({
                    count: 1,
                    pList: cur_pList,
                    preList: cur_preList
                })
            } else {
                this.setState({
                    count: this.state.count + 1
                })
            }
            if (this.state.prex != 0 || this.state.prey != 0) {
                if (this.state.count === 1) {
                    this.setState({
                        prex: e.nativeEvent.layerX,
                        prey: e.nativeEvent.layerY,
                        sx: this.state.prex,
                        sy: this.state.prey
                    })
                    cur_preList.push({
                        px: this.state.prex,
                        py: this.state.prey
                    })
                } else if (this.state.count === 3) {
                    this.setState({
                        prex: 0,
                        prey: 0
                    })
                    cur_preList.push({
                        px: this.state.prex,
                        py: this.state.prey
                    })
                } else {
                    this.setState({
                        prex: e.nativeEvent.layerX,
                        prey: e.nativeEvent.layerY
                    })
                    cur_preList.push({
                        px: this.state.prex,
                        py: this.state.prey
                    })
                }
            } else {
                this.setState({
                    prex: e.nativeEvent.layerX,
                    prey: e.nativeEvent.layerY
                })
                cur_preList.push({
                    px: this.state.prex,
                    py: this.state.prey
                })
            }
        } else {
            let px = [];
            let py = [];
            let nowx = e.nativeEvent.layerX;
            let nowy = e.nativeEvent.layerY;
            this.state.machine_data.map((mvalue, mindex) => {
                mvalue.point.map((pvalue, pindex) => {
                    px.push(pvalue.px)
                    py.push(pvalue.py)
                })
                let r1 = this.getCross(px[0 + mindex * 4], py[0 + mindex * 4], px[1 + mindex * 4], py[1 + mindex * 4], nowx, nowy);
                let r2 = this.getCross(px[2 + mindex * 4], py[2 + mindex * 4], px[3 + mindex * 4], py[3 + mindex * 4], nowx, nowy);
                let r3 = this.getCross(px[1 + mindex * 4], py[1 + mindex * 4], px[2 + mindex * 4], py[2 + mindex * 4], nowx, nowy);
                let r4 = this.getCross(px[3 + mindex * 4], py[3 + mindex * 4], px[0 + mindex * 4], py[0 + mindex * 4], nowx, nowy);
                let r5 = r1 * r2
                let r6 = r3 * r4
                if (r5 >= 0 && r6 >= 0) {
                    console.log('success', this.state.machine_data[mindex]['machine_id'])
                    this.props.mapLocationHandler(this.state.machine_data[mindex]['machine_id']);
                }
            })
        }
    }

    getCross(p1x, p1y, p2x, p2y, px, py) {
        return (p2x - p1x) * (py - p1y) - (px - p1x) * (p2y - p1y);
    }

    handleMouseUp(e) {
        if (this.state.drawStatus) {
            let overlap = false;
            if (this.state.count === 4) {
                let nowList = [];
                let cur_nowList = [];
                this.state.pList.map((pvalue, pindex) => {
                    cur_nowList = []
                    cur_nowList.push(pvalue.px, pvalue.py)
                    nowList.push(cur_nowList)
                })
                this.state.machine_data.map((mvalue, mindex) => {
                    let checkList = [];
                    let cur_checkList = [];
                    mvalue.point.map((pvalue, pindex) => {
                        cur_checkList = []
                        cur_checkList.push(pvalue.px, pvalue.py)
                        checkList.push(cur_checkList)
                    })
                    let polygonA = checkList
                    let polygonB = nowList
                    let polygonInPolygon = geometric.polygonInPolygon(polygonA, polygonB)
                    let polygonIntersectsPolygon = geometric.polygonIntersectsPolygon(polygonA, polygonB)
                    if (polygonInPolygon || polygonIntersectsPolygon) {
                        overlap = true;
                    }
                    console.log(overlap, mindex + 1)
                })
                let cur_machine_data = this.state.machine_data
                if (!overlap) {
                    axios.post('/RFID/position', {
                        floor_id: this.state.selected_floor_id,
                        point_list: this.state.pList,
                        canvas_width: parseInt(this.state.canvasWidth),
                        canvas_height: parseInt(this.state.canvasHeight),
                    })
                        .then((response) => {
                            cur_machine_data.push({
                                machine_id: response.data.data,
                                point: this.state.pList,
                                prepoint: this.state.preList,
                                canvas_width: parseInt(this.state.canvasWidth),
                                canvas_height: parseInt(this.state.canvasHeight)
                            })
                            this.setState({
                                pList: [],
                                preList: [],
                                machine_data: cur_machine_data
                            })
                        })
                        .catch((error) => {
                            console.log(error)
                        })
                } else {
                    alert('overlaped')
                    const ctx = this.upRef.current.getContext('2d');
                    ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
                    this.setState({
                        pList: [],
                        preList: []
                    })
                }
            }
        }
    }

    handleMouseMove(e) {
        if (this.state.drawStatus) {
            if (this.state.prex != 0 || this.state.prey != 0) {
                const ctx = this.upRef.current.getContext('2d');
                ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
                this.state.pList.map((pvalue, pindex) => {
                    this.state.preList.map((prevalue, preindex) => {
                        if (pindex == preindex && prevalue.px != 0 && prevalue.py != 0) {
                            line({ ctx, x: prevalue.px, y: prevalue.py, x1: pvalue.px, y1: pvalue.py });
                        }
                    })
                })
                line({ ctx, x: this.state.prex, y: this.state.prey, x1: e.nativeEvent.layerX, y1: e.nativeEvent.layerY });
                if (this.state.count === 3) {
                    line({ ctx, x: this.state.sx, y: this.state.sy, x1: e.nativeEvent.layerX, y1: e.nativeEvent.layerY });
                }
            }
        }
    }

    deleteBtn = (e) => {
        let cur_machine_data = this.state.machine_data;
        axios.delete('/RFID/position', {
            data: {
                machine_id: this.state.machine_data[e.target.id]['machine_id']
            }
        })
            .then(response => {
                cur_machine_data.splice(e.target.id, 1);
                this.setState({
                    machine_data: cur_machine_data
                })
                const ctx = this.upRef.current.getContext('2d');
                const midctx = this.midRef.current.getContext('2d');
                ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
                midctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
            })
            .catch(error => console.log(error))
    }

    drawBtn(e) {
        const upctx = this.upRef.current.getContext('2d');
        const ctx = this.midRef.current.getContext('2d');
        upctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
        ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
        let x = 0, y = 0, x1 = 0, y1 = 0;
        let draw_x = 0, draw_y = 0;
        let canvas_width = 0, canvas_height = 0;
        this.state.machine_data.map((mvalue, mindex) => {
            if (e.target.id == mindex) {
                canvas_width = mvalue.canvas_width;
                canvas_height = mvalue.canvas_height;
                mvalue.point.map((pvalue, pindex) => {
                    draw_x = this.state.canvasWidth / canvas_width
                    draw_y = this.state.canvasHeight / canvas_height
                    mvalue.prepoint.map((prevalue, preindex) => {
                        if (pindex == 0) {
                            x = pvalue.px;
                            y = pvalue.py;
                        }
                        if (pindex == 3) {
                            x1 = pvalue.px;
                            y1 = pvalue.py;
                        }
                        if (prevalue.px != 0 || prevalue.py != 0) {
                            if (pindex == preindex) {
                                line({ ctx, x: prevalue.px * draw_x, y: prevalue.py * draw_y, x1: pvalue.px * draw_x, y1: pvalue.py * draw_y });
                            }
                        }
                    })
                })
                line({ ctx, x: x * draw_x, y: y * draw_y, x1: x1 * draw_x, y1: y1 * draw_y });
            }
        })
    }

    displayAll = (e) => {
        if (!this.state.displayCheck) {
            const upctx = this.upRef.current.getContext('2d');
            const ctx = this.midRef.current.getContext('2d');
            upctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight);
            ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight);
            this.setState({
                displayCheck: true,
                displayBtnName: ''
            })
            let x = 0, y = 0, x1 = 0, y1 = 0;
            let draw_x = 0, draw_y = 0;
            let canvas_width = 0, canvas_height = 0;
            this.state.machine_data.map((mvalue, mindex) => {
                canvas_width = mvalue.canvas_width;
                canvas_height = mvalue.canvas_height;
                mvalue.point.map((pvalue, pindex) => {
                    draw_x = this.state.canvasWidth / canvas_width
                    draw_y = this.state.canvasHeight / canvas_height
                    mvalue.prepoint.map((prevalue, preindex) => {
                        if (pindex == 0) {
                            x = pvalue.px;
                            y = pvalue.py;
                        }
                        if (pindex == 3) {
                            x1 = pvalue.px;
                            y1 = pvalue.py;
                        }
                        if (prevalue.px != 0 || prevalue.py != 0) {
                            if (pindex == preindex) {
                                line({ ctx, x: prevalue.px * draw_x, y: prevalue.py * draw_y, x1: pvalue.px * draw_x, y1: pvalue.py * draw_y });
                            }
                        }
                    })
                })
                line({ ctx, x: x * draw_x, y: y * draw_y, x1: x1 * draw_x, y1: y1 * draw_y });
            })
        } else {
            const upctx = this.upRef.current.getContext('2d');
            const ctx = this.midRef.current.getContext('2d');
            upctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight);
            ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight);
            this.setState({
                displayCheck: false,
                displayBtnName: ''
            })
        }
    }

    saveBtn = (e) => {
        console.log(this.state.machine_data)
        axios.patch('/RFID/position',
            this.state.machine_data
        )
            .then((response) => console.log(response))
            .catch((error) => console.log(error))
        this.setState({
            btnSaveHidden: true,
            FormControlDisable: true
        })
    }

    onChangeFormControl = (e) => {
        let index = e.target.getAttribute('index');
        let cur_machine_data = this.state.machine_data
        cur_machine_data[index].machine_code = e.target.value
        this.setState({
            machine_data: cur_machine_data
        })
    }

    btnDraw = (e) => {
        const ctx = this.upRef.current.getContext('2d');
        ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
        if (!this.state.drawStatus) {
            this.setState({
                drawStatus: true,
            })
            Object.assign(e.target.style, { width: 'auto', background: "#5e789f", color: "white", borderColor: "#5e789f" })
        } else {
            this.setState({
                drawStatus: false,
            })
            Object.assign(e.target.style, { width: 'auto', background: "white", color: "#5e789f", borderColor: "#5e789f", fontWeight: "bold", borderWidth: "medium" })

        }
    }

    addFloorBtn = (e) => {
        if (this.state.uploadHidden) {
            this.setState({
                inputHidden: false,
                addBtnHidden: true
            })
        } else {
            this.setState({
                inputHidden: true
            })
        }
    }

    cancelAddFloorName = (e) => {
        this.setState({
            inputHidden: true,
            addBtnHidden: false
        })
    }

    addFloorName = (e) => {
        axios.post('/RFID/floor/new', {
            floor_name: this.state.floorName
        })
            .then(response => {
                this.setState({
                    new_floor_id: response.data.floor_id
                })
                let cur_floor_data = this.state.floor_data;
                cur_floor_data.push({
                    floor_id: this.state.new_floor_id,
                    floor_name: this.state.floorName
                })
                this.setState({
                    floor_data: cur_floor_data,
                    uploadHidden: false,
                    inputHidden: true
                })
            })
            .catch(function (error) {
                console.log(error)
            })
    }

    onChangeInputData = (e) => {
        this.setState({
            floorName: e.target.value
        })
    }

    selectOption = (e) => {
        let floor_id = e.target.value;
        this.updateCanvas()
        const ctx = this.upRef.current.getContext('2d');
        ctx.clearRect(0, 0, this.state.canvasWidth, this.state.canvasHeight)
        this.setState({
            canvasHidden: false,
            FormControlDisable: true,
            btnSaveHidden: true,
            selected_floor_id: floor_id,
        })
        let cur_machine_data;
        let point, prepoint;
        axios.get('/RFID/position', {
            params: {
                floor_id: floor_id
            }
        })
            .then((response) => {
                cur_machine_data = [];
                response.data.map((value, index) => {
                    point = [];
                    prepoint = [];
                    let px1 = parseInt(value.point_1_x, 10);
                    let py1 = parseInt(value.point_1_y, 10);
                    let px2 = parseInt(value.point_2_x, 10);
                    let py2 = parseInt(value.point_2_y, 10);
                    let px3 = parseInt(value.point_3_x, 10);
                    let py3 = parseInt(value.point_3_y, 10);
                    let px4 = parseInt(value.point_4_x, 10);
                    let py4 = parseInt(value.point_4_y, 10);
                    point.push({ px: px1, py: py1 })
                    point.push({ px: px2, py: py2 })
                    point.push({ px: px3, py: py3 })
                    point.push({ px: px4, py: py4 })
                    prepoint.push({ px: 0, py: 0 })
                    prepoint.push({ px: px1, py: py1 })
                    prepoint.push({ px: px2, py: py2 })
                    prepoint.push({ px: px3, py: py3 })
                    cur_machine_data.push({
                        machine_id: value.machine_id,
                        machine_code: value.machine_code,
                        canvas_width: value.canvas_width,
                        canvas_height: value.canvas_height,
                        point: point,
                        prepoint: prepoint
                    })
                })
                this.setState({
                    machine_data: cur_machine_data
                })
            })
            .catch((error) => console.log(error))
    }

    uploadBack = (e) => {
        this.setState({
            uploadHidden: true,
            addBtnHidden: false
        })
    }

    render() {
        let request_data = {
            floor_id: this.state.new_floor_id
        }
        return (
            <Container fluid>
                <Row className="mt-2" md='auto'>
                    <Col xs="auto" hidden={this.state.addBtnHidden || this.state.editMode}>
                        <Button variant='light' style={{ width: 'auto', background: "white", color: "#5e789f", borderColor: "#5e789f", fontWeight: "bold", borderWidth: "medium" }} onClick={this.addFloorBtn.bind(this)}></Button>
                    </Col>
                    <Col xs="auto" hidden={this.state.inputHidden}>
                        <InputGroup>
                            <Form.Control
                                placeholder=""
                                value={this.state.floorName}
                                onChange={this.onChangeInputData.bind(this)}
                            />
                            <Button variant='light' style={{ width: 'auto', background: "white", color: "#5e789f", borderColor: "#5e789f", fontWeight: "bold", borderWidth: "medium" }} onClick={this.cancelAddFloorName.bind(this)}></Button>
                            <Button variant='light' style={{ width: 'auto', background: "#5e789f", color: "white", borderColor: "#5e789f", borderWidth: "medium" }} onClick={this.addFloorName.bind(this)}></Button>
                        </InputGroup>
                    </Col>
                    <Col xs="auto">
                        <Form.Select onChange={this.selectOption.bind(this)} ref={this.selectRef}>
                            <option value='0' disabled></option>
                            {this.state.floor_data.map((value, index) => {
                                return <option key={value.floor_name} value={value.floor_id}>{value.floor_name}</option>
                            })}
                        </Form.Select>
                    </Col>
                    <Col xs='auto' hidden={this.state.editMode}>
                        <Button variant='light' style={{ width: 'auto', background: "white", color: "#5e789f", borderColor: "#5e789f", fontWeight: "bold", borderWidth: "medium" }} onClick={this.btnDraw.bind(this)}></Button>
                    </Col>
                    <Col xs="auto" >
                        <Button variant="light" style={{ width: 'auto', background: "white", color: "#5e789f", borderColor: "#5e789f", fontWeight: "bold", borderWidth: "medium" }} onClick={this.displayAll.bind(this)}>{this.state.displayBtnName}</Button>
                    </Col>
                    <Col xs="auto" hidden={this.state.editMode}>
                        <Button variant='light' style={{ width: 'auto', background: "#5e789f", color: "white", borderWidth: "medium", borderColor: "#5e789f" }} onClick={this.saveBtn.bind(this)}></Button>
                    </Col>
                </Row>
                <Row>
                    <Col xs='auto' className="mt-2" hidden={this.state.uploadHidden}>
                        <UploadFile allowType='image/png' height='60px' request_data={request_data} API_location='/RFID/floor_image' parentCallback={this.uploadBack.bind(this)} />
                    </Col>
                </Row>
                <Row className="my-1" hidden={this.state.editMode} xs='auto' sm={2} md={3} lg={4} xl='auto'>
                    {this.state.machine_data.map((mvalue, mindex) => {
                        return (
                            <Col key={mindex} className="mt-2">
                                <Card style={{ width: '15rem' }}>
                                    <Card.Body className='text-center'>
                                        <Form className='row'>
                                            <Col className='col-2'>
                                                <Button key={mindex} id={mindex} variant='outline-secondary' onClick={this.drawBtn.bind(this)}>{mindex + 1}</Button>
                                            </Col>
                                            <Col className='col-8'>
                                                <Form.Control placeholder="" index={mindex} value={mvalue.machine_code} onChange={this.onChangeFormControl.bind(this)} />
                                            </Col>
                                            <Col className='mt-2 col-2'>
                                                <button type="button" className="btn-close pr-3" aria-label="Close" id={mindex} onClick={this.deleteBtn.bind(this)}></button>
                                            </Col>
                                        </Form>
                                    </Card.Body>
                                </Card>
                            </Col>
                        )
                    })}
                </Row>
                <Row className="my-2">
                    <Col style={{
                        position: 'relative'
                    }}>
                        <canvas
                            ref={this.downRef}
                            style={{
                                border: '1px solid',
                                objectFit: 'contain',
                                width: '98%',
                                height: '98%'
                            }}
                            width={this.state.canvasWidth}
                            height={this.state.canvasHeight}
                        />
                        <canvas
                            ref={this.midRef}
                            style={{
                                backgroundColor: 'transparent',
                                position: 'absolute',
                                left: 0,
                                top: 0,
                                objectFit: 'contain',
                                width: '98%',
                                height: '98%'
                            }}
                            width={this.state.canvasWidth}
                            height={this.state.canvasHeight}
                        />
                        <canvas
                            ref={this.upRef}
                            style={{
                                backgroundColor: 'transparent',
                                position: 'absolute',
                                left: 0,
                                top: 0,
                                zindex: 1,
                                objectFit: 'contain',
                                width: '98%',
                                height: '98%'
                            }}
                            width={this.state.canvasWidth}
                            height={this.state.canvasHeight}
                            onMouseDown={this.handleMouseDown.bind(this)}
                            onMouseUp={this.handleMouseUp.bind(this)}
                            onMouseMove={this.handleMouseMove.bind(this)}
                        />
                    </Col>
                </Row>
            </Container >
        );
    }
}

export default MapLocation;